<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>新增售后订单</span>

            </div>
             <el-form id='form' ref="form" class="demo-form-inline" label-position='left' :rules='rules' :model="form" label-width="120px">

                <el-form-item label="输入用户账号:" prop='id_username'>
                    <el-input v-model="form.id_username"></el-input>
                </el-form-item>
                <p class='tips'>选择需要添加的商品:</p>
                <div v-for='(_item,_index) in 4' :key='_index'>
                <el-form-item :label="'商品'+(_index+1)+':'" prop='Type' class='form-line'>
                    <el-select  v-model="form['id_product_select'+(_index+1)]" placeholder="请选择">
                       <el-option
                                v-for="(item, index) in list"
                                :label="item.Name+'（剩余库存：'+item.DefaultAttr.InStock+'）'"
                                :value="item.ID"
                                :key="index">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="数量:" prop='Name'  width='50%' class='form-line'>
                    <el-input v-model="form['id_product_num'+(_index+1)]"></el-input>
                </el-form-item>
                </div>
                <el-form-item>
                    <el-button type="primary"  @click="onSubmit">提交</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Home",
    data() {
        var validateAccount = (rule, value, callback) => {
            if (value == ""||!value) {
                callback("账户不能为空!");
            }else {
                 this.$api("/admin/user/account_exist", {
                     account:value,
                     errorHandle:true,
                 }).then(res => {
                     if(res.code !=0){
                         callback(res.msg);
                     }else{
                         callback();
                     }
                        
                    
                });
            }
        };
        return {
             rules: {
                    id_username: [
                        { required: true, validator:validateAccount, trigger: 'blur' }
                    ],
             },
            form:{},
            list:""
        };
    },
    computed: {

    },
    methods: {
        getProductList(){
                this.$api('/admin/product/list', {
                    status: '-1',
                    pageNo: 0,
                    pageSize: 100
                }).then(res => {
                let d = res.data
                this.list = d.list
        });
        },
        onSubmit(){
            this.$refs.form.validate(valid => {
                if(!valid){
                    return
                }
            if(!this.form.id_product_select1||!this.form.id_product_num1){
                this.$message({
                    type:'error',
                    message:'商品1不能为空！'
                })
                return;
            }
            let params = {
                notice:'新增售后订单成功！'
            };
            Object.assign(params,this.form)
            this.$api('/admin/after/add_after_buy_order', params).then(res => {
                if(res.code == 0){
                    this.$refs.form.resetFields();
                    this.form = {}
                }
            })
            })
        }
    },
    created() {
        this.getProductList();
        // this.getFacilityDetail(16)
    }
};
</script>

<style lang='scss' scoped>
.tips{
        font-size: 16px;
    color: red;
    margin: 10px 0;
    margin-bottom: 20px;
}
.form-line{
    width: 540px;
    display: inline-block;
}
</style>



